<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <!-- gutter col -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            class="input-with-select"
            v-model="queryInfo.query"
            clearable
            @clear="getOrderList()"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getOrderList()"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderList" style="width: 100%" border stripe>
        <!-- column -->
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="order_number" label="">
        </el-table-column>
        <el-table-column prop="order_price" label=""> </el-table-column>
        <el-table-column prop="order_pay" label="">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.order_pay === '0'"
              ></el-tag
            >
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="order_status" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.order_status === '0'"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <!-- {{scope.row}} -->
            <!--       -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditAddrBox(scope.row.id)"
            ></el-button>
            <!--       "-->
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="showProgressBox(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5, 10, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editAddrDialogVisible"
      width="50%"
      @close="editAddrDialogClosed"
    >
      <el-form
        :model="addressForm"
        :rules="addressFormRoles"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="cityData"
            v-model="addressForm.address1"
            :props="{ expandTrigger: 'hover' }"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editAddrDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editAddrDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressDialogVisible"
      width="50%"
      @close="progressDialogClosed"
    >
      <el-timeline :reverse="false">
        <el-timeline-item
          v-for="(p, index) in progressInfo"
          :key="index"
          :timestamp="p.time"
        >
          {{ p.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata.js'

export default {
  name: 'Order',
  data() {
    return {
      orderList: [],
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
      },
      total: 0,
      editAddrDialogVisible: false,
      addressForm: {
        address1: [],
        address2: '',
      },
      addressFormRoles: {
        address1: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        address2: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
      cityData: cityData, // cityData  
      progressDialogVisible: false,
      progressInfo: [],
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo,
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.orderList = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    showEditAddrBox(id) {
      this.editAddrDialogVisible = true
    },
    editAddrDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    },
    async showProgressBox() {
      // 
      this.progressInfo = [
        {
          time: '2018-05-10 09:39:00  ',
          ftime: '2018-05-10 09:39:00',
          context: ',,',
          location: '',
        },

        {
          time: '2018-05-10 07:32:00  ',
          ftime: '2018-05-10 07:32:00',
          context: ' ',
          location: '',
        },
        {
          time: '2018-05-10 02:03:00  ',
          ftime: '2018-05-10 02:03:00',
          context:
            ', ',
          location: '',
        },
        {
          time: '2018-05-09 23:05:00  ',
          ftime: '2018-05-09 23:05:00',
          context: ' ',
          location: '',
        },
      ]
      console.log(this.progressInfo)
      this.progressDialogVisible = true
    },
    progressDialogClosed() {},
  },
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>